.mixitup-controls {
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
  li {
    display: inline-block;
    margin: 0 0 20px 0;
  }
  button {
    margin: 0 5px;
    padding: 8px 16px;
    background: #ffffff;
    font: 14px/18px 'Open Sans', Arial, Helvetica, sans-serif;
    font-weight: 700;
    color: #000000;
    border: 0 solid;
    border-radius: 5px;
    transition: all .4s;
    &:hover,
    &.active-button {
      background: #0EB493;
      padding: 8px 16px;
      color: #ffffff;
    }
  }
}

.mixitup__item {
  position: relative;
  max-width: 400px;
  max-height: 295px;
  overflow: hidden;
}

.item__content {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  padding: 25% 0 0 0;
  text-align: center;
  background: rgba(0, 0, 0, .8);
  transition: all .6s;
  opacity: 0;
  &:hover {
    opacity: 1;
  }
  a {
    font: 14px/18px 'Open Sans', Arial, Helvetica, sans-serif;
    font-weight: 400;
    color: #ffffff;
    display: inline-block;
    text-decoration: none;
    padding: 10px 20px;
    margin: 0 15% 20px 15%;
    background: #0EB493;
    border: 0 solid;
    border-radius: 5px;
  }
}

.popup-content {
  max-width: 600px;
  text-align: center;
  margin: auto;
  background: #ffffff;
  padding: 20px 0 0 0;
  font: 14px/18px 'Open Sans', Arial, Helvetica, sans-serif;
