/*==========  Desktop First Method  ==========*/

/* Large Devices, Wide Screens */
@media only screen and (max-width: 1200px) {
  .swiper-container{
    max-height: 650px;
  }
  .title_theme{
    h2{
      &:after{
        left: 435px;
      }
    }
  }
  .line-right {
    right: 140px;
  }

  .left {
    .tag-line{
      left: 90px;
    }
  }
  .form-box{
    input[type="submit"]{
      margin: 0 11% 0 0;
    }
  }
}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {
  .title_theme{
    h2{
      &:after{
        left: 325px;
      }
    }
  }
  .wrapper_page {
    width: 100%;
  }
  .hamburger {
    margin-top: 22px;
  }
  .slider-social-box{
    left: 53%;
  }
  .container-about{
    padding: 0 15px;
  }
  .image-wrapper{
    &:before{
      display: none;
    }
    .image-box{
      display: block;
    }
  }
  .right {
    .line-box{
      display: none;
    }
  }

  .left {
    .line-box{
      display: none;
    }
  }
  .form-box{
    input[type="text"],
    input[type="email"]{
      margin: 0 23px 30px 0;
      width: 335px !important;
    }
    textarea{
      width: 700px !important;
      height: 40px !important;
    }
    input[type="submit"]{
      margin: 0 3% 0 0;
    }
  }
}

@media only screen and (max-width: 820px) {
  .swiper-container {
    display: none;
  }
  .bg-image-box {
    display: block;
  }
}

/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
  .title_theme{
    h2{
      &:after{
        left: 50%;
        margin-left: -35px;
      }
    }
  }
  .box-logo__portfolio {
    display: none;
  }
  .header {
    opacity: 0.7;
  }
  .hamburger {
    margin-top: 22px;
    display: block;
    z-index: 10;
  }
  #nav {
    ul {
      display: none;
      position: absolute;
      right: 0;
      left: 0;
      top: 73px;
      text-align: center;
      opacity: 1;
      background-color: #101636;
      z-index: 3;
      .active {
        border-top: none;
        opacity: 1;
        background: #606686;
      }
      li {
        display: block;
        margin: 0;
        border-top: none;
        &:hover {
          border-top: none;
          opacity: 1;
          background: #606686;
        }
        a {
          padding: 20px 40px;
          width: 100%;
          &:hover {
            opacity: 1;
          }
        }
      }
    }
  }
  .slider-title-box {
    left: 65%;
    margin-left: -290px;
    h1 {
      font: 36px/40px 'Patua One', Arial, Helvetica, sans-serif;
    }
    h2 {
      font: 16px/22px 'Alice', Arial, Helvetica, sans-serif;
      &:before{
        top: 71px;
        right: 348px;
      }
      &:after{
        top: 71px;
        right: 44px;
      }
    }
  }
  .about-block_settings{
    margin-bottom: 92px;
  }
  .about_theme{
    margin-bottom: 0;
    padding: 0 15px;
  }
  .form-block{
    padding: 0 30px 100px 30px;
  }
  .form-box{
    input[type="text"],
    input[type="email"]{
      margin: 0 23px 30px 0;
      width: 100% !important;
    }
    textarea{
      width: 100% !important;
      height: 40px !important;
    }
    input[type="submit"]{
      margin: 0;
    }
  }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) {
  .slider-title-box {
    left: 66%;
    margin-left: -200px;
    h2{
      &:before{
        top: 55px;
        right: 285px;
      }
      &:after{
        top: 55px;
        right: -4px;
      }
    }
  }
  .slider-title-box h1 {
    font: 22px/26px "Patua One",Arial,Helvetica,sans-serif;
  }
  .slider-social-box {
    left: 57%;
  }
  .item__content{
    padding: 18% 0 0 0;
    a{
      padding: 5px 10px;
    }
  }
  .about__content{
    position: relative;
    width: 100%;
    &:last-child{
      &:after{
        opacity: 0;
      }
    }
    &:after{
      position: absolute;
      content:'';
      width: 100px;
      height: 2px;
      bottom: -40px;
      left: 50%;
      margin-left: -50px;
      background: #0eb493;
    }
  }
  .about-block_settings:last-child .about_theme .about__content{
    &:after{
      display: none;
    }
  }
  .image-wrapper{
    display: none;
  }
}

